header {
  background-color: #282c34;
  padding: 20px 0;
  color: white;
}
header div {
  display: flex;
  justify-content: space-between;
  align-items: center;
}

.logo img {
  max-width: 230px;
  height: auto;
}

.nav-menu {
  display: flex;
  align-items: center;
}

.hamburger-menu {
  display: none;
}
.menu-bg {
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  background-color: rgba(0, 0, 0, 0.33);
}
.bar {
  width: 30px;
  height: 3px;
  background-color: white;
  margin: 5px 0;
  transition: transform 0.3s ease-in-out, background-color 0.3s ease;
}

.menu-items {
  list-style-type: none;
  padding: 0;
  display: flex;
  gap: 20px;
}
.menu-items .display-only-responsive {
  display: none;
}

.menu-items a {
  color: white;
  text-decoration: none;
  padding: 10px;
  border: 1px solid rgba(255, 255, 255, 0.524);
  border-radius: 5px;
  transition: background-color 0.3s ease, border-color 0.3s ease,
    color 0.3s ease;
  cursor: pointer;
}

.menu-items a:hover {
  background-color: #61dafb;
  color: #282c34;
}

.menu-items li a {
  color: white;
  text-decoration: none;
  font-size: 15px;
  font-weight: bold;
  padding: 5px 15px;
  border-radius: 5px;
}

@media screen and (max-width: 800px) {
  .hamburger-menu {
    display: flex;
    align-items: center;
    justify-content: center;
    cursor: pointer;
    z-index: 1000;
    background-color: #282c34;
    padding: 5px;
    border-radius: 5px;
  }

  .hamburger-menu img {
    width: 30px;
    height: auto;
    color: white;
  }

  .menu-items {
    position: absolute;
    top: 40px;
    right: 20px;
    background-color: white;

    border-radius: 10px;
    z-index: 999;
    flex-direction: column;
    display: none;
    width: 200px;
    padding: 10px;
    box-shadow: 0px 8px 16px rgba(0, 0, 0, 0.2); /* Ombra */
  }

  .menu-items.active {
    display: flex;
    animation: dropdown 0.5s ease forwards;
  }
  .menu-items a {
    color: black;
    border: none;
    transition: none;
  }

  .menu-items li {
    padding: 16px 30px;
    text-align: center;
    margin-left: 2px;
    margin-right: 2px;
    border: 1px solid white;
    cursor: pointer;
  }

  .menu-items li a {
    color: #282c34;
    text-decoration: none;
    font-size: 1.2em;
    font-weight: bold;
  }

  .menu-items li:hover {
    margin-left: 2px;
    margin-right: 2px;

    border-radius: 9px;
    color: white;
    text-align: center;
    padding: 16px 30px;
    background-color: #61dafb;
  }

  .menu-items li:hover a {
    color: white;
  }

  .logo img {
    max-width: 200px;
    height: auto;
  }
  header {
    background-color: #282c34;
    padding: 10px 0;

    color: white;
    position: absolute;
    width: 100%;
  }

  .curriculum-button {
    display: inline-block;
    padding: 10px 20px;
    margin-top: 20px;
    background-color: #ff6600; 
    color: #ffffff;
    text-decoration: none;
    border-radius: 5px;
    transition: background-color 0.3s;
  }

  .curriculum-button:hover {
    background-color: #cc5200; 
  }
}
