

.about-container {
  display: flex;
  flex-direction: column;
  align-items: center;
  text-align: center;
  padding-top: 100px;
  padding-bottom: 30px;
}

.Presentation {
  font-size: 24px;
  margin-bottom: 10px;
  color: rgb(0, 0, 0);
  font-weight: 500;
}

.my-description{
  margin-top: -9px;

}
.Myname {
  font-size: 24px;
  color: #282c34;
}

.front-end-developer {
  display: inline-block;
  padding: 10px 20px;
  border-radius: 5px;
  margin-top: 10px;
  font-size: 22px;
  font-weight: bold;
  color: white;
  background-color: #61dafb;
  
  transition: none;
 
}

.image-container {
  margin-top: 20px;
}

.img_programmer {
  max-width: 100%;
  height: auto;

}



@media screen and (min-width: 800px) {
  .about-container {
    flex-direction: row;
    justify-content: space-between;
    align-items: center;
    text-align: left;
  }

  .Presentation {
    font-size: 36px;
    margin-bottom: 0px;
  }

  .my-description {
    margin-top: 0;
  }

  .front-end-developer {
    font-size: 32px;
  }

  .curriculum-button {
    display: inline-block; /* Mostra il pulsante solo nella versione desktop */
    padding: 10px 20px;
    margin-top: 10px;
    font-size: 18px;
    color: #fff;
    background-color: #007bff;
    border: none;
    border-radius: 5px;
    text-decoration: none;
    transition: background-color 0.3s ease, transform 0.3s ease;
  }

  .curriculum-button:hover {
    background-color: #0056b3;
    transform: scale(1.05);
  }

  .curriculum-button:active {
    transform: scale(0.95);
  }

  .image-container {
    max-width: 50%;
    margin-top: 0;
  }

  .img_programmer {
    margin-left: 0;
  }
}
@media screen and (max-width: 800px) {
.img_programmer {
    display: none;
  }
}
