/* Stile per la sezione dei progetti */
#projects {
  padding: 0px 0; 
  text-align: center;
  background-color: #282c34; /* Sfondo principale della pagina */
}

.project-container {
  background-color: #282c34; /* Sfondo della sezione del titolo e descrizione */
}

.project-title {
  padding: 0px 20px;
  color: white; /* Colore testo bianco per contrastare con lo sfondo scuro */
  margin-bottom: 40px; /* Mantiene un margine inferiore per separare il titolo dai progetti */
  font-size: 2em;
}

.project-description {
  padding: 20px;
  color: white; /* Colore testo bianco */
  margin-bottom: 60px; /* Mantiene spazio sotto la descrizione */
  max-width: 50%;
  margin: 0 auto;
  font-size: 1.2em;
}

/* Nuova sezione con sfondo bianco per i progetti */
.project-list-container {
  background-color: white; /* Sfondo bianco per i progetti */
  padding: 50px 100px; /* Aumentato il padding per avvicinare i progetti al footer */
}

.project-list {
  display: flex;
  justify-content: center; /* Centra i progetti orizzontalmente */
  gap: 50px; /* Spazio tra i progetti */
  flex-wrap: wrap; /* Permette ai progetti di andare su una nuova riga se necessario */
}

.project-card {
  position: relative;
  width: 380px; /* Aumenta la larghezza per migliorare la visibilità dell'anteprima */
  border: 3px solid #ddd;
  overflow: hidden;
  box-shadow: 0 4px 8px rgba(0, 0, 0, 0.1);
  transition: transform 0.3s ease-out, box-shadow 0.3s ease-out;
  background-color: white;
  cursor: pointer;
  top: 0px;
}

/* Animazione al passaggio del mouse sulla card */
.project-card:hover {
  transform: translateY(-10px); /* Solleva la card */
  box-shadow: 0 8px 16px rgba(0, 0, 0, 0.2); /* Ombra più pronunciata */
}

/* Stile per le immagini delle card */
.project-image {
  width: 100%;
  height: auto;
  display: block;
}

/* Dettagli aggiuntivi per ogni progetto */
.project-details {
  padding: 15px;
  color: #333;
  text-align: left;
}

.project-details h3 {
  font-size: 1.5em;
  margin-bottom: 10px;
}

.project-details p {
  font-size: 1em;
  margin-bottom: 20px;
}

/* Pulsante di azione */
.project-button {
  padding: 10px 20px;
  background-color: #61dafb;
  color: white;
  text-transform: uppercase;
  border: none;
  cursor: pointer;
  border-radius: 5px;
  transition: background-color 0.3s ease-out, transform 0.3s ease-out, box-shadow 0.3s ease-out;
  width: 100%;
  text-align: center;
}

/* Animazione hover del pulsante */
.project-button:hover {
  background-color: #4da1c0; /* Cambio colore di sfondo */
  transform: translateY(-5px) scale(1.05); /* Sollevamento e leggera espansione */
  box-shadow: 0 4px 10px rgba(0, 0, 0, 0.2); /* Aggiunge un'ombra più evidente */
}

/* Stile per schermi piccoli */
@media screen and (max-width: 800px) {
  .project-description {
    max-width: 100%;
    font-size: 1em; /* Riduzione della dimensione del testo per migliorare la leggibilità */
    margin-bottom: 40px; /* Riduce lo spazio sotto la descrizione */
  }

  .project-list {
    flex-direction: column;
    align-items: center;
    gap: 30px; /* Riduce lo spazio tra le card per una migliore disposizione */
  }

  .project-card {
    width: 100%; /* Aumenta la larghezza per riempire la larghezza disponibile */
    max-width: 320px; /* Limita la larghezza massima della card */
    height: auto; /* Permette alla card di adattarsi al contenuto */
    margin-bottom: 20px; /* Aggiunge un margine inferiore per separare le card */
    border: 2px solid #ddd; /* Leggero ridimensionamento del bordo per adattarsi alla nuova dimensione */
  }

  .project-image {
    height: 180px; /* Imposta un'altezza fissa per le immagini per evitare che si allunghino */
    object-fit: cover; /* Mantiene le proporzioni corrette delle immagini all'interno della card */
  }

  .project-details {
    padding: 10px; /* Riduce il padding interno per una disposizione più compatta */
  }

  .project-details h3 {
    font-size: 1.2em; /* Riduce la dimensione del titolo per adattarsi meglio agli schermi piccoli */
    margin-bottom: 8px;
  }

  .project-details p {
    font-size: 0.9em; /* Riduce leggermente la dimensione del testo per una migliore leggibilità */
    margin-bottom: 15px;
  }

  .project-button {
    padding: 8px 16px; /* Riduce la dimensione del pulsante per adattarsi meglio agli schermi piccoli */
    font-size: 0.9em; /* Riduce la dimensione del testo del pulsante */
  }
}