.technologies-section {
  text-align: center;
  color: #282c34;
  background-color: #fff;
  padding: 50px 20px;
  margin-top: 20px;
}

.technologies-section h2 {
  font-size: 2em;
  margin-bottom: 20px;
}

.technologies-description {
  font-size: 1.2em;
  margin-bottom: 30px;
  max-width: 800px;
  margin-left: auto;
  margin-right: auto;
}

.technologies-container {
  display: flex;
  flex-wrap: nowrap;
  justify-content: center;
  overflow-x: auto;
  gap: 20px;
}

.technologies-container::-webkit-scrollbar {
  display: none;
}

.skill-card {
  background-color: #fff;
  border: 2px solid #282c34;
  border-radius: 8px;
  box-shadow: 0 4px 8px rgba(0, 0, 0, 0.1);
  padding: 20px;
  width: 250px;
  transition: transform 0.3s, box-shadow 0.3s;
  display: flex;
  flex-direction: column;
  align-items: center;
}

.skill-card:hover {
  transform: translateY(-10px);
  box-shadow: 0 8px 16px rgba(0, 0, 0, 0.2);
}

.skill-card:hover .icon {
  color: #61dafb;
}
.skill-card:hover .icon-html,
.skill-card:hover .title-html {
  color: #e34d27;
}
.skill-card:hover .icon-css,
.skill-card:hover .title-css {
  color: #274de4;
}
.skill-card:hover .icon-javascript,
.skill-card:hover .title-javascript {
  color: #f7e018;
}
.skill-card:hover .icon-react,
.skill-card:hover .title-react {
  color: #61dafb;
}
.skill-card:hover .icon-redux,
.skill-card:hover .title-redux {
  color: #7749bd;
}
.skill-card:hover .icon-material-ui,
.skill-card:hover .title-material-ui {
  color: #02f2ff;
}
.skill-card:hover .icon-visual,
.skill-card:hover .title-visual {
  color: #0076b8;
}
.skill-card:hover .icon-github,
.skill-card:hover .title-github {
  color: orange;
}

.skill-card .icon {
  font-size: 4em;
  color: #282c34;
  margin-bottom: 10px;
  transition: color 0.3s ease;
}
.skill-card .title {
  transition: color 0.3s ease;
}

.skill-card h3 {
  margin-top: 0;
  font-size: 1.5em;
}

.skill-card p {
  color: #666;
  font-size: 1em;
  text-align: center;
}

@media (max-width: 800px) {
  .technologies-container {
    flex-direction: column;
    align-items: center;
  }
}
