#skills {
  padding: 50px;
  text-align: center;
  background-color: #282c34;
  color: #ffffff;
}

#skills ul {
  list-style-type: none;
  padding: 0;
}

#skills ul li {
  font-size: 1.2em;
  margin: 10px 0;
}

.skills-section {
  text-align: center;
  color: white;
  margin-top: -20px;
  margin-bottom: 60px;
}

.skills-section h2 {
  font-size: 2em;
  margin-bottom: 20px;
}

.skills-description {
  font-size: 1.2em;
  margin-bottom: 30px;
  color: #fff;
  max-width: 800px;
  margin-left: auto;
  margin-right: auto;
}

.skills-container {
  display: flex;
  flex-wrap: nowrap;
  justify-content: center;
  overflow-x: auto;
  gap: 20px;
}

.skills-container::-webkit-scrollbar {
  display: none;
}

.skill-card {
  background-color: #fff;
  border-radius: 8px;
  box-shadow: 0 4px 8px rgba(0, 0, 0, 0.1);
  padding: 20px;
  width: 250px;
  transition: transform 0.3s, box-shadow 0.3s;
  display: flex;
  flex-direction: column;
  align-items: center;
}

.skill-card:hover {
  transform: translateY(-10px);
  box-shadow: 0 8px 16px rgba(0, 0, 0, 0.2);
}

.skill-card:hover .icon {
  color: initial;
}

.skill-card .icon {
  font-size: 4em;
  color: #61dafb;
  margin-bottom: 10px;
  transition: color 0.3s ease;
}

.skill-card h3 {
  margin-top: 0;
  font-size: 1.5em;
}

.skill-card p {
  color: #666;
  font-size: 1em;
  text-align: center;
}

@media (max-width: 800px) {
  .skills-container {
    flex-direction: column;
    align-items: center;
  }
}
